import axios from 'axios';

import {
    SELECT_SCIRT,
    CHECK_GAME,
    RESET_GAME,
    SET_SCORE,
    CHANGE_TIME,
    START_TIMER,
    STOP_TIMER,
    RESET_TIMER,
    INIT_CARDS,
    TURN_CARD,
    CHECK_CARD,
    SEND_DATA_ERROR,
    FETCH_RECORDS_SUCCESS,
    FETCH_RECORDS_ERROR,
    FETCH_RECORDS_REQUEST,
    SORT_RESULTS,
    DELETE_RESULTS,
} from '../constants';

export const selectScirt = id => ({
    type: SELECT_SCIRT,
    payload: id,
});

export const checkGame = cards => ({
    type: CHECK_GAME,
    payload: cards,
});

export const resetGame = () => ({
    type: RESET_GAME,
});

export const setScore = time => ({
    type: SET_SCORE,
    payload: time,
});

export const startTimer = func => ({
    type: START_TIMER,
    payload: func,
});

export const changeTime = () => ({
    type: CHANGE_TIME,
});

export const stopTimer = id => ({
    type: STOP_TIMER,
    payload: id,
});

export const resetTimer = () => ({
    type: RESET_TIMER,
});

export const initCards = () => ({
    type: INIT_CARDS,
});

export const turnCard = id => ({
    type: TURN_CARD,
    payload: id,
});

export const checkCard = () => dispatch => {
    return new Promise(resolve => {
        setTimeout(() => {
            dispatch({
                type: CHECK_CARD,
            });
            resolve();
        }, 500);
    });
};

export const sendData = (name, email, score) => async dispatch => {
    try {
        await axios({
            method: 'POST',
            url: 'https://mmg-score.herokuapp.com/',
            headers: {
                'Content-Type': 'application/json',
            },
            data: {
                username: name,
                email: email,
                score: score,
            },
        });
    } catch (error) {
        dispatch({
            type: SEND_DATA_ERROR,
            payload: error,
        });
    }
};

export const fetchRecords = () => async dispatch => {
    try {
        dispatch({
            type: FETCH_RECORDS_REQUEST,
        });
        const response = await axios({
            method: 'GET',
            url: 'https://mmg-score.herokuapp.com/',
        });
        dispatch({
            type: FETCH_RECORDS_SUCCESS,
            payload: response.data.result,
        });
    } catch (error) {
        dispatch({
            type: FETCH_RECORDS_ERROR,
            payload: error,
        });
    }
};

export const sortResults = results => ({
    type: SORT_RESULTS,
    payload: results,
});

export const deleteResults = () => ({
    type: DELETE_RESULTS,
});



